.homework-havaread-content {
  margin: 30px;
  background: #fff;
  min-height: calc(100vh - 120px);
}
.homework-havaread-content .ant-btn {
  border: none;
  background: none;
  color: #3A89FD;
  box-shadow: none;
}
.homework-havaread-content .ant-input-search {
  margin: 10px 0;
}
.homework-havaread-content .have-read-studentList {
  background: #fff;
}
.homework-havaread-content .ant-table-thead > tr > th,
.homework-havaread-content .ant-table-tbody > tr > td {
  text-align: center !important;
}
.homework-havaread-content .homework-haveread-top {
  padding: 0 30px;
  height: 60px;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
  align-items: center;
}
.homework-havaread-content .ant-input-search input {
  float: right;
  width: 260px;
  height: 27px;
  background: #F3F3F3;
  border-radius: 14px;
  border: none;
}
.homework-havaread-content .homework-haveread-list {
  padding: 0 30px;
  min-height: calc(100vh - 300px);
}
.homework-havaread-content .homework-haveread-list .ant-table-row {
  height: 46px !important;
}
.homework-havaread-content .homework-haveread-list .ant-table-row:nth-child(odd) {
  background: #F7F9FB;
}
.homework-havaread-content .homework-haveread-list tr:hover td {
  background: none !important;
}
.homework-havaread-content .homework-haveread-list th {
  background: #fff !important;
}
.homework-havaread-content .homework-haveread-list th,
.homework-havaread-content .homework-haveread-list td {
  text-align: center !important;
  padding: 0 16px;
  border: none;
}
.homework-havaread-content .homework-haveread-list .ant-table-placeholder {
  border: none;
}
.text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
  cursor: pointer;
}
